import { DeckLayer } from './community/index';
import { VesselState } from "./VesselState";
import { createVesselsLayer } from "./createVesselsLayer";

export function updateVesselsLayer(deck: DeckLayer, vessels: Record<string, VesselState>, zoom: number) {
    const newData: VesselState[] = [];
    Object.keys(vessels).forEach((key) => {
        const v = vessels[key];
        if (v.longitude !== undefined && v.latitude !== undefined) { // фильтр!
            newData.push(v);
        }
    });
    deck._deck?.setProps({
        layers: [
            createVesselsLayer(newData, zoom),
        ]
    });
}