import { VesselState } from './VesselState';

export function getVesselColor(vessel: VesselState): [number, number, number, number] {
    switch (vessel.typeOfShipAndCargoType) {
        case 60: // Passenger
        case 69:
            return [0, 0, 255, 255]; // Синий

        case 70: // Cargo
        case 79:
            return [20, 112, 20, 255]; // Зелёный

        case 80: // Tanker
        case 89:
            return [255, 49, 37, 255]; // Красный

        case 30: // Fishing
            return [247, 153, 124, 255]; // Оранжевый

        case 31: // Tug
            return [0, 255, 255, 255]; // Фиолетовый

        case 32: // Pleasure Craft
        case 36: // Yacht
            return [227, 58, 255, 255]; // Розовый

        case 50: // Pilot vessels, Search and Rescue
        case 51:
        case 52:
            return [255, 255, 0, 255]; // Жёлтый

        case 90: // High-Speed Craft
            return [0, 255, 255, 255]; // Голубой (бирюзовый)

        default:
            return [64, 64, 64, 255]; // Серый для неизвестных типов
    }
}
